body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.card {
    background: #fff;
    border-radius: 2px;
    height: 300px;
    margin: 1rem;
    position: relative;
    width: 325px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
    cursor: pointer;
  }
  
  .card > .img-container {
    height: 100%;
    overflow: hidden;
    text-align: center;
    border: 10px solid #6CADDC;
    background:#6CADDC;
    
  }
  .card > .img-container > img {
    width: 80%;
   
  }
  .card:hover { -webkit-transform: scale(1.1); transform: scale(1.1); }
