.wrapper {
    padding-top: 50px;
    background: #ECEFF1;
    width: 90%;
    height: 100%;
    margin: 0 auto 20px auto;
    display: flex;
    flex-flow: row wrap;
    padding: 20px;
    justify-content: space-around;
    align-content: flex-start;
    overflow: auto;
  }
  
.outerwrapper {
    padding-top: 50px;
    background: #ECEFF1;
    width:80%;
    height: 100%;
    margin: 0 auto;
    display: flex;
    flex-flow: row wrap;
    padding: 20px;
    justify-content: space-around;
    align-content: flex-start;
    overflow: auto;
  }
  
.navbar {
    width: 100%;
    height: 200px;
    background-color: #2A77AE;
    text-align: center;
    font-size: 50px;
    font-family: 'Bowlby One', cursive;
    color: white;
    background: linear-gradient(0deg, #6CADDC  10%, rgb(109, 230, 244) 100%);
    margin-bottom: 50px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
}

h1 {
    text-shadow: 0 6px 9px black;
    font-size: 60px;
}
h2{
    text-shadow: 0 3px 6px black;
}
.footer {
    width: 100%;
    height: 70px;
    
    text-align: center;
    background:  linear-gradient(180deg, #6CADDC  10%, rgb(109, 230, 244) 100%);
   
    
}

p {
    font-size: 14px;  
    color: white;
    padding: 10px;
    text-shadow: 0 3px 6px black;

}
.title {
  width: 100%;
  text-align: center;
  margin-bottom: 50px;
}
